<template>
    <div class="tools">
        <i class="glyphicon glyphicon-plus" @click="addArticle"></i>
        <i class="glyphicon glyphicon-star" 
            @click="toggleArticle"
            :class="{isFav: activeArticle && activeArticle.isFavorite}"></i>
        <i class="glyphicon glyphicon-remove" @click="deleteArticle"></i>
    </div>
</template>
<script>
import { mapMutations, mapState } from 'vuex'
export default {
    computed: {
        ...mapState(['activeArticle'])
    },
    methods: {
        ...mapMutations(['addArticle', 'toggleArticle', 'deleteArticle'])
    },
}
</script>



<style scoped>
    .tools {
        float: left;
        width: 80px;
        height: 100%;
        background-color: #30414D;
        color: #767676;
        padding: 35px 25px 25px 25px;
    }

    .tools > i {
        font-size: 30px;
        margin-bottom: 35px;
        cursor: pointer;
        opacity: 0.8;
        transition: opacity 0.5s ease;
    }
    .isFav {
        color: gold;
    }
</style>



