<template>
    <div class="note-list">
        <div class="header">
            <h2>NOTE-APP</h2>
            <div class="btn-group btn-group-justified" role="group" aria-label="...">
                <div class="btn-group" role="group">
                    <button 
                        type="button" 
                        class="btn btn-default active"
                        :class="{active: flag}"
                        @click="flag = true">All Notes</button>
                </div>
                <div class="btn-group" role="group">
                    <button 
                        type="button" 
                        class="btn btn-default"
                        :class="{active: !flag}"
                        @click="flag = false">Favorites</button>
                </div>
            </div>
        </div>
        <div class="list">
            <div 
                v-for="(art, index) in nowList" 
                :key="index"
                :class="{isActive: art.time === activeArticle.time}"
                @click="changeActiveArticle(art)">{{ art.title }}</div>
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations, mapGetters } from 'vuex'
export default {
    data() {
        return {
            flag: true
        }
    },
    computed: {
        ...mapState(['articleList', 'activeArticle']),
        ...mapGetters(['favoriteArticleList']),
        nowList() {
            return this.flag ? this.articleList : this.favoriteArticleList
        }
    },
    methods: {
        ...mapMutations(['changeActiveArticle'])
    }
}
</script>



<style scoped>
    .note-list {
        float: left;
        width: 300px;
        height: 100%;
        background-color: #F5F5F5;
        font-family: 'Raleway', sans-serif;
        font-weight: 400;
    }
    .header {
        height: 17%;
        padding: 5px 25px 25px 25px;
    }
    h2 {
        font-weight: 300;
        text-align: center;
        font-size: 22px;
        padding-bottom: 8px;
    }
    .list {
        height: 83%;
        /* background: rgb(0, 183, 255); */
        width: 100%;
        overflow: auto;
        padding: 0;
    }
    .list > div {
        height: 35px;
        line-height: 35px;
        padding: 0px 15px;
        font-weight: 300;
        cursor: pointer;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    .isActive {
        background: #337ab7;
        color: white;
    }
</style>



