<template>
  <div class="wrapper">
    <tools/>
    <note-list></note-list>
    <editor/>
  </div>
</template>

<script>
import tools from './components/tools.vue'
import noteList from './components/noteList.vue'
import editor from './components/editor.vue'
export default {
  components: {
    tools,
    noteList,
    editor,
  }
}
</script>

<style>
  * {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  }
  body {
    position: relative;
    height:100%;
  }
  html {
    height: 100%;
  }
  .wrapper {
    height: 100%;
  }

</style>


