<template>
    <div class="editor" v-if="activeArticle">
        <div class="form-group head">
            <label for="Title">Title</label>
            <input 
                type="text" 
                class="form-control" 
                id="exampleInputEmail1" 
                placeholder="please input"
                :value="activeArticle.title"
                @input="update"
                >
        </div>
        <div class="form-group content">
            <label for="article">Article</label>
            <textarea 
                class="form-control" 
                rows="28" 
                placeholder="please input"
                :value="activeArticle.content"
                @input="e => updateArticle({content: e.target.value})"></textarea>
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
export default {
    computed: {
        ...mapState(['activeArticle'])
    },
    methods: {
        ...mapMutations(['updateArticle']),
        update(e) {
            this.updateArticle({title: e.target.value})
        }
    }
}
</script>

<style scoped>
    .editor {
        height: 100%;
        margin-left: 380px;
        padding: 20px 10px;
    }
</style>



