import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        articleList: [],
        activeArticle: null,
    },
    mutations: {
        addArticle(state) {
            let article = {
                title: 'New note',
                content: '',
                isFavorite: false,
                time: new Date().getTime(),
            }
            state.articleList.push(article);
            state.activeArticle = article;
        },
        changeActiveArticle(state, art) {
            state.activeArticle = art;
        },
        toggleArticle(state) {
            state.activeArticle.isFavorite = !state.activeArticle.isFavorite
        },
        deleteArticle(state) {
            if(state.articleList.length !== 0) {
                state.articleList = state.articleList.filter(art => art !== state.activeArticle),
                state.activeArticle = state.articleList [0]
            }
        },
        updateArticle(state, art) {
            // Object.assign(state.activeArticle, art)
            if(art.hasOwnProperty('title')) {
                state.activeArticle.title = art.title;
            }
            if(art.hasOwnProperty('content')) {
                state.activeArticle.content = art.content;
            }
        }
    },
    getters: {
        favoriteArticleList(state) {
            return state.articleList.filter(art => art.isFavorite)
        }
    }
})


